'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { useRouter } from 'next/navigation';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Badge } from '@/components/ui/badge';
import { getEvaluations, saveEvaluation, Evaluation } from '@/lib/mockData';

export default function EvaluationsPage() {
  const { user, isLoading } = useAuth();
  const router = useRouter();
  const [evaluations, setEvaluations] = useState<Evaluation[]>([]);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [activeTab, setActiveTab] = useState('student');
  const [formData, setFormData] = useState({
    evaluatedName: '',
    rating: '',
    comments: ''
  });

  useEffect(() => {
    if (!isLoading && !user) {
      router.push('/login');
      return;
    }

    if (user) {
      loadEvaluations();
    }
  }, [user, isLoading, router]);

  const loadEvaluations = () => {
    const allEvaluations = getEvaluations();
    setEvaluations(allEvaluations);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setSuccess('');
    setIsSubmitting(true);

    // Validation
    if (!formData.evaluatedName.trim() || !formData.rating || !formData.comments.trim()) {
      setError('Please fill in all required fields');
      setIsSubmitting(false);
      return;
    }

    const rating = parseInt(formData.rating);
    if (isNaN(rating) || rating < 1 || rating > 5) {
      setError('Please select a valid rating between 1 and 5');
      setIsSubmitting(false);
      return;
    }

    try {
      const success = saveEvaluation({
        type: activeTab as 'student' | 'teacher',
        evaluatedName: formData.evaluatedName.trim(),
        rating,
        comments: formData.comments.trim(),
        evaluatedBy: user?.name || 'Unknown'
      });

      if (success) {
        setSuccess(`${activeTab === 'student' ? 'Student' : 'Teacher'} evaluation submitted successfully!`);
        setFormData({
          evaluatedName: '',
          rating: '',
          comments: ''
        });
        loadEvaluations();
      } else {
        setError('Failed to save evaluation. Please try again.');
      }
    } catch (err) {
      setError('An error occurred while saving the evaluation.');
    } finally {
      setIsSubmitting(false);
    }
  };

  const getRatingColor = (rating: number) => {
    if (rating >= 4) return 'bg-green-100 text-green-800';
    if (rating >= 3) return 'bg-yellow-100 text-yellow-800';
    return 'bg-red-100 text-red-800';
  };

  const getRatingText = (rating: number) => {
    switch (rating) {
      case 5: return 'Excellent';
      case 4: return 'Good';
      case 3: return 'Average';
      case 2: return 'Below Average';
      case 1: return 'Poor';
      default: return 'Unknown';
    }
  };

  const canEvaluate = user?.role === 'admin' || user?.role === 'teacher';
  const canEvaluateTeachers = user?.role === 'admin';

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-lg">Loading...</div>
      </div>
    );
  }

  if (!user) {
    return null;
  }

  const studentEvaluations = evaluations.filter(e => e.type === 'student');
  const teacherEvaluations = evaluations.filter(e => e.type === 'teacher');

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="space-y-2">
        <h1 className="text-3xl font-bold text-gray-900">Evaluations</h1>
        <p className="text-gray-600">
          {canEvaluate 
            ? 'Evaluate student and teacher performance' 
            : 'View evaluations and feedback'
          }
        </p>
      </div>

      <Tabs value={activeTab} onValueChange={setActiveTab} className="space-y-6">
        <TabsList className="grid w-full grid-cols-2">
          <TabsTrigger value="student">Student Evaluations</TabsTrigger>
          <TabsTrigger value="teacher">Teacher Evaluations</TabsTrigger>
        </TabsList>

        {/* Student Evaluations Tab */}
        <TabsContent value="student" className="space-y-6">
          {/* Evaluation Form */}
          {canEvaluate && (
            <Card>
              <CardHeader>
                <CardTitle>Evaluate Student</CardTitle>
                <CardDescription>
                  Provide feedback and rating for student performance
                </CardDescription>
              </CardHeader>
              <CardContent>
                <form onSubmit={handleSubmit} className="space-y-4">
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div className="space-y-2">
                      <Label htmlFor="studentName">Student Name *</Label>
                      <Input
                        id="studentName"
                        type="text"
                        placeholder="Enter student name"
                        value={formData.evaluatedName}
                        onChange={(e) => setFormData(prev => ({ ...prev, evaluatedName: e.target.value }))}
                        required
                      />
                    </div>

                    <div className="space-y-2">
                      <Label htmlFor="rating">Rating *</Label>
                      <Select value={formData.rating} onValueChange={(value) => setFormData(prev => ({ ...prev, rating: value }))}>
                        <SelectTrigger>
                          <SelectValue placeholder="Select rating" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="5">5 - Excellent</SelectItem>
                          <SelectItem value="4">4 - Good</SelectItem>
                          <SelectItem value="3">3 - Average</SelectItem>
                          <SelectItem value="2">2 - Below Average</SelectItem>
                          <SelectItem value="1">1 - Poor</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="comments">Comments *</Label>
                    <Textarea
                      id="comments"
                      placeholder="Provide detailed feedback about the student's performance, behavior, and areas for improvement"
                      value={formData.comments}
                      onChange={(e) => setFormData(prev => ({ ...prev, comments: e.target.value }))}
                      rows={4}
                      required
                    />
                  </div>

                  {error && (
                    <div className="text-sm text-red-600 bg-red-50 p-3 rounded-md">
                      {error}
                    </div>
                  )}

                  {success && (
                    <div className="text-sm text-green-600 bg-green-50 p-3 rounded-md">
                      {success}
                    </div>
                  )}

                  <Button type="submit" disabled={isSubmitting}>
                    {isSubmitting ? 'Submitting Evaluation...' : 'Submit Student Evaluation'}
                  </Button>
                </form>
              </CardContent>
            </Card>
          )}

          {/* Student Evaluations List */}
          <Card>
            <CardHeader>
              <CardTitle>Student Evaluations</CardTitle>
              <CardDescription>
                {studentEvaluations.length === 0 
                  ? 'No student evaluations yet' 
                  : `${studentEvaluations.length} student evaluation${studentEvaluations.length === 1 ? '' : 's'} found`
                }
              </CardDescription>
            </CardHeader>
            <CardContent>
              {studentEvaluations.length === 0 ? (
                <div className="text-center py-8 text-gray-500">
                  <p>No student evaluations have been submitted yet.</p>
                  {canEvaluate && (
                    <p className="mt-2">Use the form above to submit the first evaluation.</p>
                  )}
                </div>
              ) : (
                <div className="space-y-4">
                  {studentEvaluations.map((evaluation) => (
                    <div key={evaluation.id} className="border rounded-lg p-4">
                      <div className="flex items-center justify-between mb-3">
                        <div className="flex items-center space-x-3">
                          <h3 className="font-semibold text-lg">{evaluation.evaluatedName}</h3>
                          <Badge className={getRatingColor(evaluation.rating)}>
                            {evaluation.rating}/5 - {getRatingText(evaluation.rating)}
                          </Badge>
                        </div>
                        <span className="text-sm text-gray-500">
                          {new Date(evaluation.dateEvaluated).toLocaleDateString()}
                        </span>
                      </div>
                      <p className="text-gray-700 mb-2">{evaluation.comments}</p>
                      <p className="text-sm text-gray-500">Evaluated by: {evaluation.evaluatedBy}</p>
                    </div>
                  ))}
                </div>
              )}
            </CardContent>
          </Card>
        </TabsContent>

        {/* Teacher Evaluations Tab */}
        <TabsContent value="teacher" className="space-y-6">
          {/* Teacher Evaluation Form */}
          {canEvaluateTeachers && (
            <Card>
              <CardHeader>
                <CardTitle>Evaluate Teacher</CardTitle>
                <CardDescription>
                  Provide feedback and rating for teacher performance
                </CardDescription>
              </CardHeader>
              <CardContent>
                <form onSubmit={handleSubmit} className="space-y-4">
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div className="space-y-2">
                      <Label htmlFor="teacherName">Teacher Name *</Label>
                      <Input
                        id="teacherName"
                        type="text"
                        placeholder="Enter teacher name"
                        value={formData.evaluatedName}
                        onChange={(e) => setFormData(prev => ({ ...prev, evaluatedName: e.target.value }))}
                        required
                      />
                    </div>

                    <div className="space-y-2">
                      <Label htmlFor="rating">Rating *</Label>
                      <Select value={formData.rating} onValueChange={(value) => setFormData(prev => ({ ...prev, rating: value }))}>
                        <SelectTrigger>
                          <SelectValue placeholder="Select rating" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="5">5 - Excellent</SelectItem>
                          <SelectItem value="4">4 - Good</SelectItem>
                          <SelectItem value="3">3 - Average</SelectItem>
                          <SelectItem value="2">2 - Below Average</SelectItem>
                          <SelectItem value="1">1 - Poor</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="comments">Comments *</Label>
                    <Textarea
                      id="comments"
                      placeholder="Provide detailed feedback about the teacher's performance, teaching methods, student engagement, and areas for improvement"
                      value={formData.comments}
                      onChange={(e) => setFormData(prev => ({ ...prev, comments: e.target.value }))}
                      rows={4}
                      required
                    />
                  </div>

                  {error && (
                    <div className="text-sm text-red-600 bg-red-50 p-3 rounded-md">
                      {error}
                    </div>
                  )}

                  {success && (
                    <div className="text-sm text-green-600 bg-green-50 p-3 rounded-md">
                      {success}
                    </div>
                  )}

                  <Button type="submit" disabled={isSubmitting}>
                    {isSubmitting ? 'Submitting Evaluation...' : 'Submit Teacher Evaluation'}
                  </Button>
                </form>
              </CardContent>
            </Card>
          )}

          {!canEvaluateTeachers && user.role === 'teacher' && (
            <Card>
              <CardContent className="pt-6">
                <div className="text-center py-4">
                  <p className="text-gray-600">
                    Teacher evaluations can only be submitted by administrators.
                  </p>
                </div>
              </CardContent>
            </Card>
          )}

          {/* Teacher Evaluations List */}
          <Card>
            <CardHeader>
              <CardTitle>Teacher Evaluations</CardTitle>
              <CardDescription>
                {teacherEvaluations.length === 0 
                  ? 'No teacher evaluations yet' 
                  : `${teacherEvaluations.length} teacher evaluation${teacherEvaluations.length === 1 ? '' : 's'} found`
                }
              </CardDescription>
            </CardHeader>
            <CardContent>
              {teacherEvaluations.length === 0 ? (
                <div className="text-center py-8 text-gray-500">
                  <p>No teacher evaluations have been submitted yet.</p>
                  {canEvaluateTeachers && (
                    <p className="mt-2">Use the form above to submit the first evaluation.</p>
                  )}
                </div>
              ) : (
                <div className="space-y-4">
                  {teacherEvaluations.map((evaluation) => (
                    <div key={evaluation.id} className="border rounded-lg p-4">
                      <div className="flex items-center justify-between mb-3">
                        <div className="flex items-center space-x-3">
                          <h3 className="font-semibold text-lg">{evaluation.evaluatedName}</h3>
                          <Badge className={getRatingColor(evaluation.rating)}>
                            {evaluation.rating}/5 - {getRatingText(evaluation.rating)}
                          </Badge>
                        </div>
                        <span className="text-sm text-gray-500">
                          {new Date(evaluation.dateEvaluated).toLocaleDateString()}
                        </span>
                      </div>
                      <p className="text-gray-700 mb-2">{evaluation.comments}</p>
                      <p className="text-sm text-gray-500">Evaluated by: {evaluation.evaluatedBy}</p>
                    </div>
                  ))}
                </div>
              )}
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      {/* Summary Statistics */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <Card>
          <CardHeader>
            <CardTitle>Student Evaluation Summary</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-2">
              <div className="flex justify-between">
                <span>Total Evaluations:</span>
                <span className="font-semibold">{studentEvaluations.length}</span>
              </div>
              {studentEvaluations.length > 0 && (
                <>
                  <div className="flex justify-between">
                    <span>Average Rating:</span>
                    <span className="font-semibold">
                      {(studentEvaluations.reduce((sum, e) => sum + e.rating, 0) / studentEvaluations.length).toFixed(1)}/5
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span>Excellent (5/5):</span>
                    <span className="font-semibold">{studentEvaluations.filter(e => e.rating === 5).length}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>Good (4/5):</span>
                    <span className="font-semibold">{studentEvaluations.filter(e => e.rating === 4).length}</span>
                  </div>
                </>
              )}
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Teacher Evaluation Summary</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-2">
              <div className="flex justify-between">
                <span>Total Evaluations:</span>
                <span className="font-semibold">{teacherEvaluations.length}</span>
              </div>
              {teacherEvaluations.length > 0 && (
                <>
                  <div className="flex justify-between">
                    <span>Average Rating:</span>
                    <span className="font-semibold">
                      {(teacherEvaluations.reduce((sum, e) => sum + e.rating, 0) / teacherEvaluations.length).toFixed(1)}/5
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span>Excellent (5/5):</span>
                    <span className="font-semibold">{teacherEvaluations.filter(e => e.rating === 5).length}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>Good (4/5):</span>
                    <span className="font-semibold">{teacherEvaluations.filter(e => e.rating === 4).length}</span>
                  </div>
                </>
              )}
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
